.fallingPetals {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.fallingPetals img {
    position: fixed;
    display: block;
}

.fallingPetals img:nth-child(1) {
    left: 0%;
    -webkit-animation: petals 12s linear infinite;
            animation: petals 12s linear infinite;
    -webkit-animation-delay: -5s;
            animation-delay: -5s;
}

.fallingPetals img:nth-child(2) {
    left: 10%;
    -webkit-animation: petals 9s linear infinite;
            animation: petals 9s linear infinite;
}

