@-webkit-keyframes petals {
    0% {top: -10%; opacity: 1; -webkit-transform: translateX(30px) rotate(0deg); transform: translateX(30px) rotate(0deg);}
    25% {opacity: 1; -webkit-transform: translateX(-20px) rotate(45deg); transform: translateX(-20px) rotate(45deg);}
    50% {opacity: 1; -webkit-transform: translateX(20px) rotate(90deg); transform: translateX(20px) rotate(90deg);}
    75% {opacity: 1; -webkit-transform: translateX(-10px) rotate(135deg); transform: translateX(-10px) rotate(135deg);}
    100% {top: 105%; opacity: 0; -webkit-transform: translateX(10px) rotate(180deg); transform: translateX(10px) rotate(180deg);}
}

@keyframes petals {
    0% {top: -10%; opacity: 1; -webkit-transform: translateX(30px) rotate(0deg); transform: translateX(30px) rotate(0deg);}
    25% {opacity: 1; -webkit-transform: translateX(-20px) rotate(45deg); transform: translateX(-20px) rotate(45deg);}
    50% {opacity: 1; -webkit-transform: translateX(20px) rotate(90deg); transform: translateX(20px) rotate(90deg);}
    75% {opacity: 1; -webkit-transform: translateX(-10px) rotate(135deg); transform: translateX(-10px) rotate(135deg);}
    100% {top: 105%; opacity: 0; -webkit-transform: translateX(10px) rotate(180deg); transform: translateX(10px) rotate(180deg);}
}

@-webkit-keyframes petals2 {
    0% {top: -10%; opacity: 1; -webkit-transform: translateX(-15px) rotate(0deg) scale(2); transform: translateX(-15px) rotate(0deg) scale(2);}
    25% {opacity: 1; -webkit-transform: translateX(30px) rotate(45deg) scale(2); transform: translateX(30px) rotate(45deg) scale(2);}
    50% {opacity: 1; -webkit-transform: translateX(-20px) rotate(90deg) scale(2); transform: translateX(-20px) rotate(90deg) scale(2);}
    75% {opacity: 1; -webkit-transform: translateX(20px) rotate(135deg) scale(2); transform: translateX(20px) rotate(135deg) scale(2);}
    100% {top: 105%; opacity: 0; -webkit-transform: translateX(-10px) rotate(180deg) scale(2); transform: translateX(-10px) rotate(180deg) scale(2);}
}

@keyframes petals2 {
    0% {top: -10%; opacity: 1; -webkit-transform: translateX(-15px) rotate(0deg) scale(2); transform: translateX(-15px) rotate(0deg) scale(2);}
    25% {opacity: 1; -webkit-transform: translateX(30px) rotate(45deg) scale(2); transform: translateX(30px) rotate(45deg) scale(2);}
    50% {opacity: 1; -webkit-transform: translateX(-20px) rotate(90deg) scale(2); transform: translateX(-20px) rotate(90deg) scale(2);}
    75% {opacity: 1; -webkit-transform: translateX(20px) rotate(135deg) scale(2); transform: translateX(20px) rotate(135deg) scale(2);}
    100% {top: 105%; opacity: 0; -webkit-transform: translateX(-10px) rotate(180deg) scale(2); transform: translateX(-10px) rotate(180deg) scale(2);}
}

