.header {
    display: flex;
    position: relative;
    width: 100%;
    height: 15vh;
    background-color: #FFF0F0;
    align-items: center;
    font-family: "Space Mono", monospace;
    box-shadow: 0 0 15px 5px #FFBDBD;
    z-index: 1;
}

.logo {
    margin-left: 1%;
    height: 95%;
    object-fit: contain;
}

.name {
    font-size: 3vmin;
}

.navBar {
    position: absolute;
    right: 0;
    display: flex;
    background-color: #FFF0F0;
    height: 100%;
    width: 40%;
}

.navBar #home, #about, #experience, #resume {
    display: flex;
    color: black;
    text-decoration: none;
    font-size: 3vmin;
    height: 100%;
    width: 21%;
    justify-content: center;
    align-items: center;
}

.navBar #experience {
    width: 36%;
}

.navBar #resume {
    width: 22%;
}

.navBar #icon {
    display: flex;
    color: black;
    text-decoration: none;
    font-size: 3vmin;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    background-color: #FFF0F0;
    border: none;
    cursor: pointer;
    padding: 0;
    margin: 0;
}

.navBar #icon, #item1, #item2, #item3, #item4 {
    display: none;
}

.navBar a:hover {
    background-color: #FFBDBD;
}

.active {
    text-shadow: 0px 0px 10px #FF2F2F;
}

@media screen and (max-width: 1000px) {
    .navBar #home, #about, #experience, #resume {
        display: none;
    }

    .navBar {
        justify-content: flex-end;
    }

    .navBar .dropdown {
        position: relative !important;
        right: 0 !important;
        height: 100% !important;
        width: 30% !important;
    }

    .navBar #icon {
        height: 100%;
    }

    .navBar .menu {
        height: 5vmin;
        width: 5vmin;
    }

    .navBar #icon, #item1, #item2, #item3, #item4 {
        display: flex;
        color: black;
        text-decoration: none;
        font-size: 3vmin;
        justify-content: center;
        align-items: center;
        width: 100%;
    }

    .navBar #icon:hover {
        background-color: #FFBDBD;
    }

    .navBar #icon:focus, #icon:active {
        background-color: #FFF0F0;
    }

    .navBar .dropdown-menu {
        -webkit-transform: none !important;
                transform: none !important;
        margin: 0 !important;
        inset: 0 !important;
        position: static !important;
        background-color: #FFF0F0 !important;
        width: 30vmin !important;
        float: right !important;
    }

    .navBar #item1, #item2, #item3, #item4 {
        padding: 2%;
    }
}
